.add-stay {
    .img-upload-container {
        height: 42vh;
        margin-top: 24px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 50% 50%;
        width: 100%;
        // max-height: 451px;
        grid-gap: 8px;
        padding: 0;
        overflow: hidden;
        border-radius: 12px;

        & :first-child {
            grid-column: 1 / span 2;
            grid-row: 1 / span 2;
        }
    }

    .img-upload-container > * {
        background-color: $gray-clr3;
    }

    .add-stay-form {
        background-color: $gray-clr8;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-end;
    }

    .add-stay-button-container {
        position: relative;
        height: 70px;
        width: 160px;
    }
}
